*{
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;

  background-color: #f4a261;
}
  

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#drum-machine{
  display: flex;
  padding: 2em;
  border-radius: 7px;
  background-color: #CF4747;
}

#drum-container{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 0.5em;
}

.drum-pad{
  outline:none;
  border-radius: 7px;
  width: 5rem;
  height: 5rem;
  border: 0;
  background-color: #FFB7B7;
  cursor: pointer;
  font-weight: bold;
  box-shadow: 1px 2px  white;

}

.button-active{
  background-color: #f4a261;
  transform: scale(0.99);
  box-shadow: 0px 1px  white;
}

#display-container{
  display: flex;
  flex-direction: column;
  margin-left: 2em;
}

#display{
  width: 100%;
  margin-top: 6em;
  padding: 0.5em 0;
  text-align: center;
  background-color: #FFB7B7;
  border-radius: 6px;
  font-weight: bold;
  
}

.slidecontainer{
  margin-top: 2em;
}


